import React, {Component} from 'react';
import { Grid, Cell } from 'react-mdl';

class Experiencia extends Component{
  render(){
    return(
      <Grid>
        <Cell col={4}>
          <p> {this.props.mesInicio} {this.props.añoInicio}- {this.props.mesFinal} {this.props.añoFinal}</p>
        </Cell>
        <Cell col={8}>
          <h5 style={{marginTop:'0px'}}> {this.props.empresa}</h5>
          <h6>{this.props.puesto}</h6>
          <p> {this.props.decripcion} </p> 
          </Cell>
      </Grid>
    )
  }
}

export default Experiencia;