import React, { Component } from 'react';
import { Grid, Cell,  } from 'react-mdl';
import '../components/aboutme.css';
import GifAnndy from '../img/about_me.gif'

class About extends Component {
    render(){
        return(
            <div style={{ width: '100%', margin: 'auto' }}>
            <Grid className='aboutme-grid'>
                <Cell col={12}>
                    <h1>
                        Andrea Magallanes 
                    </h1>
                    <div className= 'imagenes'>
                    <img src={GifAnndy} alt="avatar"/>
                    </div>
                    <div className= 'descripcion'>
                    <h3>
                    Mi nombre es Andrea Magallanes y soy Font-End Developer. 
                    </h3>
                    <p>
                    Soy multifacética, una apasionada por los animales, andar en bicicleta, conocer lugares andando en ella y que ama crear valor de cualquier manera posible.
                    <br>
                    </br>
                    Me encanta aprender todo el tiempo didácticamente, así como con las enseñanzas de la sabiduría y experiencia de otros.
                    <br>
                    </br>
                    <br>
                    </br>
                    Gracias a Laboratoria aprendí habilidades técnicas como HTML, CSS, JavaScript, React entre otras, pero también reforcé y aprendí soft skill como el trabajo en equipo, autoaprendizaje, comunicación. Y estoy habituada a trabajar de forma tanto autónoma como en equipo.
                    <br>
                    </br>
                    <br>
                    </br>
                    Con mi experiencia en ventas trabajando en telemarketing por más de 5 años, desarrolle liderazgo al estar a cargo de grupos de hasta 80 personas y me dejó habilidades comunicativas, excelencia en la atención al cliente y dotes para la negociación, así como comerciales y de persuasión.
                    <br>
                    </br>
                    <br>
                    </br>
                    Creo que el universo está lleno de gratas sorpresas y me encanta descubrirlas y disfrutarlas Y también estoy convencida de que una vez encuentras tu fuente de inspiración, puedes crear cosas increíbles esto me hace creativa y siempre basándome en mis valores e integridad 
                    <br>
                    </br>
                    <h6>
                    Aporto: compromiso ➕ pro actividad ➕ flexibilidad
                    </h6>
                    </p>
                    </div>
                </Cell>
            </Grid>
        </div>

    )
}
}

export default About