import React, {Component} from 'react';
import { Grid, Cell } from 'react-mdl';
import './educacion.css';

class Education extends Component{
  render(){
    return(
      <Grid>
        <Cell col={4}>
          <p> {this.props.mesInicio} {this.props.añoInicio} - {this.props.mesFinal} {this.props.añoFinal}</p>
        </Cell>
        <Cell col={8}>
          <h4 style={{marginTop:'0px'}}> {this.props.escuela} </h4>
          <p> {this.props.scuelaDescripcion} </p> 
          </Cell>
      </Grid>
     // <div><h1>{this.props.startYear}</h1></div>
    )
  }
}

export default Education;